import * as React from "react"
import { Link, graphql } from "gatsby";
import Layout from "../components/layout";
import Seo from "../components/seo";

// ページネーション
import { Pagination } from "../components/pagination"

const InformationPage = ({ data }) => (
  <Layout>
    <Seo title="インフォーメーション" />
    <div className="content__wrap">

      {/* コンテナ */}
      <div className="content__inner --sm">

        {/* 表題ブロック */}
        <div className="">
          インフォーメーション
        </div>

        {/* カードブロック */}
        {data.allMicrocmsInformation.edges.map(({ node }) => (
          <div className="">
            <div className="">
              <div className="">
                <Link to={node.informationId}>
                  {node.title}
                </Link>
              </div>
              <div className="">
                {node.date}{`  `}{node.category.category}
              </div>
              <p>{node.excerpt}</p>
            </div>
          </div>
        ))}

        {/* ページネーション */}
        <Pagination totalCount={data.allMicrocmsInformation.totalCount} />
      </div>

    </div>{/* content__wrap */}

  </Layout>
)

export default InformationPage

export const query = graphql`
query {
  allMicrocmsInformation(limit:2, skip:0, sort: {fields:[date], order:DESC}) {
    totalCount
    edges {
      node {
        category {
          category
        }
        date(formatString: "YYYY年MM月DD日")
        informationId
        title
        excerpt
      }
    }
  }
}
`